import { HTMLAttributes } from 'react';
import { AnimatedAxis, AnimatedGrid, AnimatedLineSeries, XYChart, Tooltip } from '@visx/xychart';
import classNames from 'classnames';
import { get } from 'lodash';
import { Typography } from 'components/ui';
import './index.scss';
import { GRAY_400, GRAY_600, PRIMARY_400 } from '../../../../tokens/variables';
// ----------------------------------------------------------------------
interface IChartData {
  label: string;
  value: number;
  text?: string;
}

export type LineChartProps = {
  width?: number;
  height?: number;
  background?: string;
  axisXPosition?: 'left' | 'right';
  numTicks: {
    x: number;
    y: number;
  };
  unit?: {
    x: string | null;
    y: string | null;
  };
  colorSet?: {
    axis: string;
    line: string;
    label: string;
  };
  data: IChartData[];
} & HTMLAttributes<HTMLDivElement>;

const LineChart = ({
  width = 886,
  height = 326,
  background = 'bg-white',
  axisXPosition = 'left',
  numTicks,
  unit,
  colorSet = {
    axis: GRAY_400,
    line: PRIMARY_400,
    label: GRAY_600
  },
  data = []
}: LineChartProps) => {
  const accessors = {
    labelAccessor: (data: IChartData) => data.label,
    valueAccessor: (data: IChartData) => data.value,
    textAccessor: (data: IChartData) => data.text ?? ''
  };
  return (
    <div className={classNames(background, 'relative')}>
      {unit?.x && (
        <div
          className={classNames(
            'absolute top-5 rotate-90',
            axisXPosition === 'left' ? 'left-5' : 'right-5'
          )}
        >
          <Typography variant="body" size="xs" color="text-gray-600">
            ({unit.x})
          </Typography>
        </div>
      )}
      {unit?.y && (
        <div className="absolute bottom-2 right-3">
          <Typography variant="body" size="xs" color="text-gray-600">
            ({unit.y})
          </Typography>
        </div>
      )}
      <XYChart width={width} height={height} xScale={{ type: 'band' }} yScale={{ type: 'linear' }}>
        <AnimatedAxis
          orientation="bottom"
          numTicks={numTicks.x}
          tickStroke={colorSet.axis}
          stroke={colorSet.axis}
          strokeWidth={1.5}
          top={height - 35}
          tickComponent={({ formattedValue, ...tickProps }) => (
            <text {...tickProps} fill={colorSet.label}>
              {formattedValue}
            </text>
          )}
        />
        <AnimatedAxis
          orientation={axisXPosition}
          numTicks={numTicks.y}
          hideTicks
          hideAxisLine
          tickStroke={colorSet.axis}
          stroke={colorSet.axis}
          tickComponent={({ formattedValue, ...tickProps }) => (
            <text {...tickProps} fill={colorSet.label}>
              {formattedValue}
            </text>
          )}
        />
        <AnimatedGrid
          columns={false}
          numTicks={numTicks.y}
          strokeDasharray="5"
          lineStyle={{ stroke: colorSet.axis }}
        />
        <AnimatedLineSeries
          data={data}
          dataKey={'Line'}
          stroke={colorSet.line}
          strokeWidth={2.5}
          xAccessor={accessors.labelAccessor}
          yAccessor={accessors.valueAccessor}
        />
        <Tooltip
          snapTooltipToDatumX
          snapTooltipToDatumY
          showSeriesGlyphs
          glyphStyle={{ fill: colorSet.line }}
          renderTooltip={(params) => {
            const datum = get(params.tooltipData?.datumByKey.Line, 'datum');
            return (
              <>
                <div>
                  <Typography variant="head" size="xs" color="text-gray-800">
                    {accessors.labelAccessor(datum as IChartData)}
                  </Typography>
                  <div className="flex items-center mt-1">
                    <div
                      className="w-3 h-3 rounded-full mr-1"
                      style={{ backgroundColor: colorSet.line }}
                    />
                    <Typography variant="body" size="xs" className="mr-1" color="text-gray-800">
                      {accessors.textAccessor(datum as IChartData)}
                      {accessors.textAccessor(datum as IChartData) && ' :'}
                    </Typography>
                    <Typography variant="head" size="xs" color="text-gray-800">
                      {accessors.valueAccessor(datum as IChartData)}
                    </Typography>
                  </div>
                </div>
              </>
            );
          }}
        />
      </XYChart>
    </div>
  );
};

export default LineChart;
