import {
  ComponentType,
  FC,
  ReactElement,
  ReactNode,
  cloneElement,
  createElement,
  isValidElement,
  useState
} from 'react';
import { currencyFormatter } from 'utils/formatters';

import { Group } from '@visx/group';
import { Pie } from '@visx/shape/lib';
import { Legend, LegendItem, LegendLabel } from '@visx/legend';
import { Text } from '@visx/text';
import { scaleOrdinal } from '@visx/scale';
import { Typography } from 'components/ui';

import classNames from 'classnames';

// ----------------------------------------------------------------------

export type Item = {
  label: string;
  value: number;
};

export interface DonutProps {
  width?: number;
  height?: number;
  itemSize?: number;
  background?: string;
  title?: ReactNode;
  data: {
    label: string;
    value: number;
  }[];
  colorSet?: string[];
  hasLegend?: boolean;
  legendPosition?: 'top' | 'right' | 'bottom' | 'left';
  legendDirection?: 'row' | 'column';
  renderStats?: ReactNode;
  hasActiveTool?: boolean;
}

// ----------------------------------------------------------------------

const DonutChart = ({
  width = 400,
  height = 400,
  itemSize = 56,
  background = 'transparent',
  title,
  data,
  colorSet = [],
  hasLegend = true,
  legendPosition = 'bottom',
  legendDirection = 'row',
  renderStats,
  hasActiveTool
}: DonutProps) => {
  const [active, setActive] = useState<Item | null>(null);

  const HALF_SIZE = width / 2;

  const KEYS = data && Array.isArray(data) && data.map((item) => item.label);
  const RANGE =
    data &&
    Array.isArray(data) &&
    data.map((item, index) => (
      <rect key={item.label} width="16" height="16" fill={colorSet[index]} rx={8} />
    ));

  const LEGEND_POSITION_MAPS = {
    top: 'flex-col-reverse items-center',
    right: 'flex-row',
    bottom: 'flex-col items-center',
    left: 'flex-row-reverse'
  };

  const LEGEND_DIRECTION_MAPS = {
    row: 'flex-row items-center gap-x-3',
    column: 'flex-col justify-center gap-y-3'
  };

  const colorScale = scaleOrdinal({
    domain: KEYS || [],
    range: colorSet
  });

  const shapeScale = scaleOrdinal<string, FC | ReactNode>({
    domain: KEYS || [],
    range: RANGE || []
  });

  return (
    <div className="donut-chart flex items-center gap-20">
      <div className={classNames('flex gap-8', LEGEND_POSITION_MAPS[legendPosition])}>
        <div className="shrink-0 flex">
          <svg width={width} height={height}>
            <rect x={0} y={0} width={width} height={height} fill={background} />
            <Group top={HALF_SIZE} left={HALF_SIZE}>
              <Pie
                data={data}
                pieValue={(data) => data.value}
                outerRadius={HALF_SIZE}
                innerRadius={({ data }) => {
                  const innerRadius =
                    active && active.label === data.label ? itemSize + 4 : itemSize;

                  return HALF_SIZE - innerRadius;
                }}
                padAngle={0.01}
              >
                {(pie) => {
                  return pie.arcs.map((arc) => {
                    return (
                      <g
                        key={arc.data.label}
                        onMouseEnter={() => setActive(arc.data)}
                        onMouseLeave={() => setActive(null)}
                      >
                        <path d={pie.path(arc) || ''} fill={colorScale(arc.data.label)}></path>
                      </g>
                    );
                  });
                }}
              </Pie>

              {hasActiveTool && active ? (
                <>
                  <Text
                    textAnchor="middle"
                    fill="#000"
                    fontSize={itemSize * 0.42}
                    fontWeight={700}
                    dy={itemSize * -0.25}
                    className="cursor-default"
                  >
                    {`${currencyFormatter(active.value)}원`}
                  </Text>
                  <Text
                    textAnchor="middle"
                    fill="#858794"
                    fontSize={itemSize * 0.35}
                    dy={itemSize * 0.25}
                    className="cursor-default"
                  >
                    {`${active.label} asset`}
                  </Text>
                </>
              ) : (
                <>{title || ''}</>
              )}
            </Group>
          </svg>
        </div>
        {hasLegend && (
          <div className="shrink-0 flex justify-center">
            <Legend scale={shapeScale}>
              {(labels) => (
                <div className={classNames('flex', LEGEND_DIRECTION_MAPS[legendDirection])}>
                  {labels.map((label, i) => {
                    const color = colorScale(label.datum);
                    const shape = shapeScale(label.datum);
                    const isValidEle = isValidElement(shape);
                    return (
                      <LegendItem
                        key={`legend-${i}`}
                        className="gap-[0.125rem]"
                        onClick={() => {
                          const { datum, index } = label;
                          alert(`clicked: ${JSON.stringify({ datum, color, index })}`);
                        }}
                      >
                        <svg width={16} height={16}>
                          {isValidEle
                            ? cloneElement(shape as ReactElement)
                            : createElement(shape as ComponentType<{ fill: string }>, {
                                fill: color
                              })}
                        </svg>
                        <LegendLabel align="left" margin={0} className="cursor-default">
                          <Typography size="lg">{label.text}</Typography>
                        </LegendLabel>
                      </LegendItem>
                    );
                  })}
                </div>
              )}
            </Legend>
          </div>
        )}
      </div>
      {renderStats}
    </div>
  );
};

export default DonutChart;
